.dmc-form-page {
  overflow: initial;
  margin: 8px;
  background: #fff;
  background: var(--mdc-theme-background, #fff);
  max-width: 1200px;

  .mdc-card__primary {
    padding-left: 24px;
    padding-bottom: 8px;
    position: relative;
  }

  a {
    text-decoration: none;
    color: var(--mdc-theme-primary, #3f51b5);
  }
}


.dmc-form-page__form {
  padding-left: 24px;
  padding-right: 24px;
}

.dmc-form-page__actions {
  justify-content: flex-end;
  padding: 24px;
  padding-bottom: 12px;
}

@media (max-width: 600px) {
  .dmc-form-page {
    margin: 0px;
  }
}



